/* ==========================================================================
   Header Standard style - begin
   ========================================================================== */

@media only screen and (max-width: 1300px) {
	
	.mkdf-header-standard {
		
		.mkdf-menu-area {
			padding: 0 35px;
            .mkdf-vertical-align-containers {
                padding: 0;
            }
			
			.mkdf-grid {
				width: 100%;
                .mkdf-vertical-align-containers {
                    padding: 0 35px;
                }
			}
		}
	}
}
/* ==========================================================================
   Header Standard style - end
   ========================================================================== */